<template>
  <!-- selection field from areas  -->
  <v-col cols="12">
    <v-autocomplete
      :rules="nationRules"
      v-model="nation_id"
      label="Implementation nation *"
      :items="allNation"
      item-text="name"
      item-value="id"
    >
      <template v-slot:selection="data">
        <v-chip
          class="primary"
          v-bind="data.attrs"
          :input-value="data.name"
          close
          @click="data.select"
          @click:close="remove(data.item)"
        >
          {{ data.item.name }}
        </v-chip>
      </template>
    </v-autocomplete>
  </v-col>
</template>

<script>
export default {
  mounted() {
    const result = this.allNation.map((item) => {
      return {
        name: item.name[this.lang],
        id: item.id,
      };
    });
    this.allNation=result;
  },
  props: ["nation"],
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  data() {
    return {
      nation_id: this.nation,
      nationRules: [(v) => !!v || "Choose at least nation"],
      allNation: [
        {
          name: {
            en: "Turkey",
            ar: "تركيا",
            tr: "Türkiye",
          },
          id: 1,
        },
        { name: { en: "Syria", ar: "سوريا", tr: "Suriye" }, id: 2 },
      ],
    };
  },
  methods: {
    remove() {
      this.nation_id = null;
    },
  },
  watch: {
    nation_id(vla) {
      this.$emit("changNation", vla);
    },
  },
};
</script>

